'use client';

import Link from "next/link";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { formatPrice } from "@/lib/utils";
import { ArrowRight, Sparkles } from "lucide-react";

interface ToolCardProps {
  tool: {
    id: string;
    name: string;
    slug: string;
    description?: string;
    shortDescription?: string;
    category: string;
    icon?: string;
    priceMonthly: number;
    isActive: boolean;
  };
  showSubscribeButton?: boolean;
}

export function ToolCard({ tool, showSubscribeButton = true }: ToolCardProps) {
  const categoryColors: Record<string, { bg: string; text: string; border: string }> = {
    AI_WRITING: {
      bg: "bg-blue-500/20",
      text: "text-blue-300",
      border: "border-blue-500/50",
    },
    SEO_TOOLS: {
      bg: "bg-green-500/20",
      text: "text-green-300",
      border: "border-green-500/50",
    },
    DESIGN: {
      bg: "bg-purple-500/20",
      text: "text-purple-300",
      border: "border-purple-500/50",
    },
    PRODUCTIVITY: {
      bg: "bg-orange-500/20",
      text: "text-orange-300",
      border: "border-orange-500/50",
    },
    CODE_DEV: {
      bg: "bg-pink-500/20",
      text: "text-pink-300",
      border: "border-pink-500/50",
    },
    VIDEO_AUDIO: {
      bg: "bg-indigo-500/20",
      text: "text-indigo-300",
      border: "border-indigo-500/50",
    },
    OTHER: {
      bg: "bg-gray-500/20",
      text: "text-gray-300",
      border: "border-gray-500/50",
    },
  };

  const categoryLabels: Record<string, string> = {
    AI_WRITING: "AI Writing",
    SEO_TOOLS: "SEO Tools",
    DESIGN: "Design",
    PRODUCTIVITY: "Productivity",
    CODE_DEV: "Code & Dev",
    VIDEO_AUDIO: "Video & Audio",
    OTHER: "Other",
  };

  const categoryStyle = categoryColors[tool.category] || categoryColors.OTHER;

  return (
    <Card className="group relative overflow-hidden glass border border-white/10 hover:border-purple-500/50 transition-all duration-300 hover:-translate-y-2 hover:shadow-lg hover:shadow-purple-500/20 h-full flex flex-col">
      {/* Gradient overlay on hover */}
      <div className="absolute inset-0 bg-gradient-to-br from-purple-600/0 to-blue-600/0 group-hover:from-purple-600/10 group-hover:to-blue-600/10 transition-all duration-300 pointer-events-none" />
      
      <CardHeader className="flex-1">
        {/* Category Badge */}
        <div className="flex items-center justify-between mb-3">
          <Badge 
            className={`${categoryStyle.bg} ${categoryStyle.text} ${categoryStyle.border} border glass text-xs font-medium`}
            variant="secondary"
          >
            {categoryLabels[tool.category] || tool.category}
          </Badge>
          {tool.isActive && (
            <div className="flex items-center space-x-1 text-green-400">
              <div className="h-2 w-2 rounded-full bg-green-400 animate-pulse" />
              <span className="text-xs">Active</span>
            </div>
          )}
        </div>

        {/* Icon */}
        <div className="text-6xl mb-4 group-hover:scale-110 transition-transform duration-300">
          {tool.icon || "üõ†Ô∏è"}
        </div>

        {/* Tool Name */}
        <CardTitle className="text-xl mb-2 text-white group-hover:text-purple-300 transition-colors">
          {tool.name}
        </CardTitle>

        {/* Description */}
        <CardDescription className="line-clamp-2 text-gray-400 group-hover:text-gray-300 transition-colors">
          {tool.shortDescription || tool.description || "Premium AI tool access"}
        </CardDescription>
      </CardHeader>

      <CardContent className="pt-0">
        <div className="flex items-center justify-between">
          {/* Price */}
          <div>
            <div className="text-3xl font-bold gradient-text mb-1">
              {formatPrice(tool.priceMonthly)}
            </div>
            <div className="text-sm text-gray-400">per month</div>
          </div>

          {/* Subscribe Button */}
          {showSubscribeButton && tool.isActive ? (
            <Button 
              asChild
              className="bg-gradient-to-r from-purple-600 to-blue-600 hover:from-purple-500 hover:to-blue-500 text-white border-0 shadow-lg hover:shadow-purple-500/50 transition-all duration-300 hover:scale-105"
            >
              <Link href={`/checkout/${tool.id}`} className="flex items-center">
                Subscribe
                <ArrowRight className="ml-2 h-4 w-4" />
              </Link>
            </Button>
          ) : !tool.isActive ? (
            <Badge variant="destructive" className="glass border-red-500/50 bg-red-500/20 text-red-300">
              Unavailable
            </Badge>
          ) : null}
        </div>
      </CardContent>

      {/* Shine effect on hover */}
      <div className="absolute inset-0 -translate-x-full group-hover:translate-x-full transition-transform duration-1000 bg-gradient-to-r from-transparent via-white/5 to-transparent pointer-events-none" />
    </Card>
  );
}
